import React from "react";
import styled from "styled-components";

// --------------------------------------------------

// @DESC: Default row layout
// @USE:
const UnityRow = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 0 1rem;
  justify-items: center;
`;

export function Row(props) {
  return <UnityRow>{props.children}</UnityRow>;
}

// --------------------------------------------------

// @DESC: Default column layout
// @USE:
const UnityCol = styled.div`
  display: flex;
  flex-direction: column;
`;

export function Col(props) {
  return <UnityCol>{props.children}</UnityCol>;
}

// --------------------------------------------------

// @DESC: Goes around the entire site
// @USE:
const UnityPageWrapper = styled.div`
  display: grid;
  grid-template-areas:
    "navbar"
    "content"
    "footer";
  max-width: 100vw;
  height: 100%;
  background: rgba(226, 232, 240, 0.8);
`;

export function PageWrapper(props) {
  return <UnityPageWrapper>{props.children}</UnityPageWrapper>;
}

// --------------------------------------------------

// @DESC: Goes around the main content section between navigation and footer
// @USE:
const UnityContentWrapper = styled.div`
  display: grid;
  grid-area: content;
`;

export function ContentWrapper(props) {
  return <UnityContentWrapper>{props.children}</UnityContentWrapper>;
}

// --------------------------------------------------

// @DESC: Default container
// @USE:
const UnityContainer = styled.div`
  padding: 1rem;
  width: 80%;
  margin: auto;
  color: var(--textcolor);
  > h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  h7,
  h8 {
    text-align: center;
  }
`;

export function Container(props) {
  return <UnityContainer>{props.children}</UnityContainer>;
}

// --------------------------------------------------

// @DESC: Default img avatar, circuluar image.
// @USE: <Avatar size='200px' src='https://source.unsplash.com/random'></Avatar>
const UnityAvatar = styled.img`
  border-radius: 50%;
  object-fit: cover;
  top: 0;
`;

export function Avatar(props) {
  return (
    <UnityAvatar
      src={props.src}
      alt={props.alt}
      style={{
        width: props.size,
        height: props.size,
      }}></UnityAvatar>
  );
}

// --------------------------------------------------

// @DESC: Default card
// @USE: <Avatar size='200px' src='https://source.unsplash.com/random'></Avatar>
const UnityCard = styled.div`
  height: 300px;
  width: 200px;
  padding: 1rem;
  margin: 0;
  > h2 {
    text-align: center;
    color: green;
  }
  > ul {
    list-style-type: disc;
  }
`;

export function Card(props) {
  return <UnityCard>{props.children}</UnityCard>;
}
